main {
    border-style: hidden;
}

a {
    color: black;
}

p {
    line-height: 1em;
    color: black;
    text-align: end;
    font-size: 2rem;
    font-family: 'Open Sans', sans-serif;
    font-weight: 300;
    
}

img:hover {
  box-shadow: 0 0 2px 1px rgba(0, 140, 186, 0.5);
}

#grayscale {
  
  border-radius: 50%;
  box-shadow: 5px 5px 5px lightgrey;
  /* Add the blur effect */
  filter: blur(8px);
  -webkit-filter: blur(10px);
}
#grayscale:hover {
filter: blur(0);
  -webkit-filter: blur();
}

hr {
  
  border: 0; 
  height: 1px; 
  background-image: linear-gradient(to right, #f0f0f0, #00b9ff, #59d941, #f0f0f0);
  box-shadow: 5px 5px 5px lightcoral;
}
footer h6 {
  text-align: center;
  font-family: "Open Sans";
  background-color: #D4EDF7;
  margin: auto;
}




div h3 {
  font-family: 'Open Sans', sans-serif;
  color: grey;
  font-weight: 300;
  
}

.zoomOut {
  position: relative;
  width: 100%;
}

img {
  
  display: block;
  width: 100%;
  height: auto;
  border-radius: 3%;
 
box-shadow: 15px 8px 8px 4px rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.overlay {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #347B98;
  overflow: hidden;
  width: 100%;
  height: 100%;
  transform: scale(0);
  transition: .4s ease;
  border-radius: 3%;
  opacity: .9;
  
}

.zoomOut:hover .overlay {
  transform: scale(1);
  
  
}

.text1 {
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 1.5rem;
  font-weight: 800;
}
.text2  {
  position: absolute;
  font-size: 15px;
  font-weight: 400;
  font-family: 'Open Sans', sans-serif;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  color: #341809;
}

div p a  {
  position: absolute;
  top: 80%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-align: center;
  font-size: 1.2rem;
  font-weight: 600;
  font-family: 'Open Sans', sans-serif;
  color: #B84E14;
}


